<template>
  <div @click="biu" id="app">
    <nav>
      <ul class="grid-x nav menu" style="position:fixed">
        <li>
          <router-link class :to="'/'">文章</router-link>
        </li>

        <li>
          <router-link class :to="'/gallery'">图集</router-link>
        </li>
      </ul>
    </nav>

    <div style="margin-top:50px">
      <router-view />
    </div>
  </div>
</template>
<script>
import { Howl, Howler } from "howler";
export default {
  mounted() {},
  data() {
    return {
      music: "http://106.15.183.147:3000/user/avatars/yangqi.mp3",
      clickText: ["H", "U", "A", "N", "G", "Y", "I", "C", "H", "A", "O"],
      index: 0
    };
  },
  mounted() {
    var sound = new Howl({
      src: ["http://106.15.183.147:3000/" + "user/avatars/yangqi.mp3"]
    });
    sound.once("load", function() {
      sound.play();
    });

    // Fires when the sound finishes playing.
    sound.on("end", function() {
      console.log("Finished!");
    });
  },
  methods: {
    biu(e) {
      var d = document.createElement("div");
      var txt = this.clickText[this.index++ % this.clickText.length];
      var node = document.createTextNode(txt);
      d.appendChild(node);

      d.className = "click";
      d.style.top = e.clientY + "px";
      d.style.left = e.clientX + "px";
      document.body.appendChild(d);
      setTimeout(() => {
        document.body.removeChild(d);
      }, 1000);
    }
  }
};
</script>

<style>
.nav {
  display: flex;
  width: 100vw;
  top: 0px;
  background-color: rgb(207, 221, 238);
}

.click {
  position: fixed;
  z-index: 999;
  animation: click 1.2s;
}
#can {
  height: 100vh;
  width: 100vh;
  position: fixed;
  z-index: 0;
}
.audio {
  height: 40px;
  position: fixed;
  bottom: 0;
}
@keyframes click {
  0% {
    opacity: 1;
    transform: translate(-50%, -50%);
  }

  100% {
    opacity: 0;
    color: green;

    transform: scale(1.1) translate(-50%, -50%);
  }
}
</style>
