<template>
  <div>
    <div v-for="blog in blogs" :key="blog.id">
      <router-link :to="'/post/'+blog.id" class="header" style="z-index:999">{{ blog.title }}</router-link>
      <br />
      <div class="label" v-for="tag in blog.categories" :key="tag.id">{{ tag.name }}</div>
      <div class="subheader">{{ blog.content }}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      blogs: []
    };
  },
  created() {
    this.axios.get("blog").then(rs => {
      this.blogs = rs.data;
    });
  }
};
</script>

